import React,{Component} from "react";
import { Tabs, Badge } from 'antd-mobile';
import styles from './detailList.module.scss'
import DetailCell from "../detailCell/detailCell";
const tabs = [
    { title: <Badge >点菜</Badge> },
    { title: <Badge >评价</Badge> },
    { title: <Badge >商家</Badge> },
];

class DetailList extends Component{
    static defaultProps={
        data:[]
    }


    render() {
        let {data} =this.props
        return (
            <>
                <Tabs tabs={tabs}
                      initialPage={0}
                    // onChange={(tab, index) => { console.log('onChange', index, tab); }}
                    // onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}
                >
                    <div className={styles.content}>
                        {/*点餐左侧竖条*/}
                        <div className={styles.aside}>
                            <ul>
                                {data.map((item,index)=>{
                                    return <li key={index}>{item.type}</li>
                                })}
                            </ul>
                        </div>
                        {/*点餐右侧竖条*/}
                        <div className={styles.asideRight}>
                            {data.map((item,index)=>{
                                return (
                                    <>
                                        <span className={styles.tit} key={index+'qweqw'}>{item.type}</span>
                                        {item.list.map((item1,index1)=>{
                                            return <DetailCell data={item1} key={index1+'daasd'}/>
                                        })}
                                    </>
                                )
                            })}
                        </div>
                    </div>
                    <div className={styles.content}>
                        Content of second tab
                    </div>
                    <div className={styles.content}>
                        Content of third tab
                    </div>
                </Tabs>
            </>
        );
    }
}

export default DetailList;