import React,{Component} from "react";
import  styles from './login.module.scss'
import Btn from "../../components/button/button";
import qs from "qs";
import getLogin from "../../api/login";
import {connect} from 'react-redux'


class Login extends Component{
    render() {
        let {username,password,msg,getPass,getUser,login,dispatch} = this.props
        return (
            <>
                <div className={styles.top}>
                    <img src="http://s3plus.meituan.net/v1/mss_e2821d7f0cfe4ac1bf9202ecf9590e67/cdn-prod/file:9096d347/7a5ff05b334e0debdd10954a7f5d4789.png" alt=""/>
                    <span>美团外卖</span>
                </div>
                <Btn text='账号' onChange={(el)=>{getUser(el)}} value={username}/>
                <Btn text='密码' type='password' value={password} onChange={(el)=>{getPass(el)}}/>
                <div className={styles.reg} onClick={()=>{
                    this.props.history.push("/reg")
                }}>
                    还没有账号？去注册
                </div>
                <div className={styles.btn} onClick={()=>{login({username,password})}}>
                    登录
                </div>
                <div className={styles.msg}>
                    {msg}
                </div>
            </>
        )
    }
}
export default connect(
    state=>({username:state.username,password: state.password,msg:state.msg}),
    (dispatch,ownPros)=>({
        getUser:(el)=>{
            dispatch({type:'user',payload:el.target.value})
        },
        getPass:(el)=>{
            dispatch({type:'pass',payload:el.target.value})
        },
        login:async ({username,password})=>{
            let res= await getLogin({username,password})
            if(res.data.err===0){
                ownPros.history.push('/home')
                dispatch({type:"msg",payload:""})
                dispatch({type:"user",payload:""})
                dispatch({type:"pass",payload:""})
                dispatch({type:"userMsg",payload:res.data})
                window.localStorage.setItem("user",qs.stringify(res.data))
            }else{
                dispatch({type:"msg",payload:res.data.msg})
                dispatch({type:"user",payload:""})
                dispatch({type:"pass",payload:""})
            }
        }
    })
)(Login)