import React,{Component} from "react";
import {withRouter} from "react-router-dom";
import styles from './cell.module.scss'
class Cell extends Component{
    change=()=>{
        this.props.history.push({pathname:'/detail'})
    }
    render() {
        let {data} = this.props
        return (
            <div className={styles.cell} onClick={()=>{this.props.history.push({pathname:`./detail/${data._id}`})}}>
                <div className={styles.imgbox}>
                    <img src={data.outlook} alt=""/>
                </div>
                <div className={styles.msg}>
                    <h4>{data.name}</h4>
                    <div className={styles.c}>
                        <div>
                            <span>⭐</span>
                            <span>⭐</span>
                            <span>⭐</span>
                            <span>⭐</span>
                            <span>⭐</span>

                            <span>{data.star}</span>
                        </div>
                        <p>月售{data.sell}</p>
                        <div>{data.address}</div>
                    </div>
                    <div className={styles.btm}>
                        <span>起送￥{data.qisong}|配送￥{data.peisong}</span>
                        {data.delivery && <p>{data.delivery}</p>}
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(Cell)