import React,{Component} from "react";
import {Route,Redirect,Switch} from "react-router-dom";

import Footer from "../../layouts/footer/footer";
import User from "../../pages/user/user";
import Home from "../../pages/home/home";
import Detail from "../../pages/detail/detail";
import Goods from "../../pages/goods/goods";
import Login from "../../pages/login/login";
import Reg from "../../pages/reg/reg";
import Loading from "../../pages/loading/loading";
import Nopage from "../../pages/nopage/nopage";
import {connect} from 'react-redux'


class Default extends Component{
    state={}
    static getDerivedStateFromProps=(nextProps)=>{
        if(/home|goods|user/.test(nextProps.location.pathname)){
            return {Bfooter:nextProps.dispatch({type:'UPDATE_BFOOTER',payload:true})}
        }else{
            return {Bfooter:nextProps.dispatch({type:'UPDATE_BFOOTER',payload:false})}
        }

    }

    render() {
        let {Bfooter,Bloading} = this.props
        return (
            <>

                { Bloading && <Loading/>}
                <Switch>
                    <Route path='/home' component={Home}/>
                    <Route path='/detail/:_id' component={Detail}/>
                    <Route path='/user' component={User}/>
                    <Route path='/goods' component={Goods}/>
                    <Route path='/reg' component={Reg}/>
                    <Route path='/login' component={Login}/>
                    <Redirect from='/' exact to='/home'/>
                    <Route  component={Nopage}/>
                </Switch>

                {Bfooter && <Footer/>}

            </>
        )
    }
}
export default connect(
    state=>({Bloading:state.Bloading,Bfooter:state.Bfooter})
)(Default);