import React,{Component} from "react";
import { Carousel, WingBlank } from 'antd-mobile';
import {withRouter} from "react-router-dom";
class Banner extends Component{
    state = {
        data: [
            {id:1,url:'http://p1.meituan.net/codeman/826a5ed09dab49af658c34624d75491861404.jpg'},
            {id:2,url:'http://p0.meituan.net/codeman/a97baf515235f4c5a2b1323a741e577185048.jpg'},
            {id:3,url:'https://p1.meituan.net/travelcube/01d2ab1efac6e2b7adcfcdf57b8cb5481085686.png'}
        ],
        imgHeight: "3rem",
    }
    render() {
        return (
            <WingBlank>
                <Carousel
                    autoplay={true}
                    infinite
                >
                    {this.state.data.map(val => (
                        <a
                            key={val.id}

                            style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
                        >
                            <img
                                src={val.url}
                                alt=""
                                style={{ width: '100%',height:'100%',verticalAlign: 'top' }}
                            />
                        </a>
                    ))}
                </Carousel>
            </WingBlank>
        );
    }


}

export default  withRouter(Banner);