import React,{Component} from "react";
import Banner from "../../components/swiper/banner";
import  styles from './home.module.scss'
import MyGrid from "../../components/grid/grid";
import Cell from "../../components/cell/cell";
import {updateHome} from "../../redux/actionCreators";
import {connect} from "react-redux"

class Home extends Component{
    componentDidMount() {
        this.props.dispatch(updateHome())
    }
    render() {
        let {list} = this.props
        return (
            <div className={styles.home}>
                <div className={styles.top}>
                    <div className={styles.pst}>
                        <i className='iconfont icon-dingwei1' ></i>
                        <span>上海第二工业大学</span>
                        <i className='iconfont icon-iconfontjiantou2' ></i>
                    </div>
                    <div className={styles.search}>
                        <i className='iconfont icon-fangdajing1'></i>
                        <input type="text" placeholder='请输入商家或商品名称'/>
                    </div>
                </div>
                <Banner/>
                <MyGrid/>
                {list.map((item,index)=>{
                    return <Cell data={item} key={index} />
                })}

            </div>
        )
    }
}
export default connect(
    state=>({list:state.goods})
)(Home);