import axios from'../plugins/axios'
export const add = payload => ({
    type:'add',
    payload:payload || 1
});

//home页数据请求
export const updateHome = () => dispatch => { //dispatch接受函数 需要thunk中间件
    return axios({url:`/api/goods/goods`}).then(
        res=> {
            dispatch({type:"goods",payload:res.data.data});
            return res.data.data//有回执
        }
    )
};
//详情页数据请求
export const updateDetail = (_id) => dispatch => { //dispatch接受函数 需要thunk中间件
    return axios({url:`/api/goods/goods/${_id}`}).then(
        res=> {
            dispatch({type:"detail",payload:res.data.data});
            return res.data.data//有回执
        }
    )
};
