import React,{Component} from "react";
import  styles from './user.module.scss'
import Usercell from "../../components/userCell/usercell";
import store from "../../redux";


class User extends Component{

    render() {

        return (
            <div>
                <div className={styles.top}>
                    <div className={styles.imgbox}>
                        <img src="http://p1.meituan.net/codeman/e32b47a07b818bf9a1d4086a882c18a62282.png" alt=""/>
                    </div>
                    <p>{store.getState().user.data.nikename}</p>
                </div>
                <Usercell icon='iconfont icon-hongbao1' text='美团红包'/>
                <Usercell icon='iconfont icon-dingwei' text='收货地址'/>
                <Usercell icon='iconfont icon-49' text='常见问题'/>
                <Usercell icon='iconfont icon-xieyixiangqing' text='美团协议与常见问题'/>
                <Usercell icon='iconfont icon-web-icon-' text='退出登录' onClick={()=>{
                    window.localStorage.removeItem('user')
                    this.props.history.push("/login")
                }}/>

                <div className={styles.phone}>客服电话：10109777</div>
                <p className={styles.time}>服务时间：9:00-23:00</p>
            </div>
        )
    }
}
export default User;